.todoApp {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 100%;
  height: 100%;
}

:root {
  --primary-blue: #305cde;
  --white: #fff;
  --black: #1e1e1e;
}

.flex {
  display: flex;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}

.mainHeader {
  width: 100%;
  max-width: 2000px;
  margin: 0 auto;
  padding: 2rem;
  background-color: var(--primary-blue);
  color: var(--white);
  align-items: center;
  justify-content: center;
}

.createBoxContainer {
  width: 100%;
  max-width: 2000px;
  margin: 0 auto;
  padding: 0rem 2rem 4rem;
  display: flex;
}

.inputContainer {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.newTaskLabel {
  font-size: 2rem;
}

.newTaskInput {
  padding: 1rem 2rem;
  width: 100%;
  max-width: 600px;
  border-radius: 0.5rem;
  box-shadow: none;
  border: 1px solid var(--black);
}

.createTaskBtn {
  width: 100%;
  max-width: 600px;
  padding: 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.mainContainer {
  width: 100%;
  max-width: 2000px;
  margin: 0 auto;
  height: 100%;
  padding: 4rem 2rem;
}

.todoItemContainer {
  padding: 1rem 2rem;
  border: 1px solid var(--black);
  background-color: var(--white);
  border-radius: 0.5rem;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  justify-content: space-between;
  align-items: center;
}

.buttonContainer {
  column-gap: 0.5rem;
}

.buttonContainer button {
  padding: 0.25rem;
  cursor: pointer;
  font-size: 0.7rem;
}
